@import "src/variables";

.InvisibleTasks {
  display: flex;
  background-color: white;
  border-radius: 12px;
  gap: 24px;
  padding: 24px;

  @include tablet {
    flex-direction: column;
  }

  @include mobile {
    flex-direction: column;
  }

  &Title {

    @include web {
      width: 40%;
    }

    margin: 0;
    font-size: 40px;
    color: $font-color;
    font-weight: 400;
  }

  &Content {
    border-radius: 12px;
    background-color: $background-color;
    padding: 24px;

    @include web {
      width: 60%;
    }

    margin: 0;
    font-size: 40px;
    color: $font-color;
    font-weight: 400;
    display: flex;
    flex-direction: column;

    @include web {
      gap: 24px;
      max-height: 480px;
      min-height: 480px;
    }

    @include tablet {
      gap: 16px;
      font-size: 32px;
    }

    @include mobile {
      gap: 8px;
      font-size: 16px;
    }
  }
}