@import "src/variables";

.DoubleBlock {
  display: flex;
  gap: 24px;

  @include tablet {
    flex-direction: column;
  }

  @include mobile {
    flex-direction: column;
  }

  &Body {

    background-color: white;
    border-radius: 12px;
    padding: 24px;

    @include web {
      width: 50%;
    }

    &Title {
      margin: 0;
      font-size: 40px;
      color: $font-color;
      font-weight: 400;
    }

    &Content {
      font-size: 28px;
      line-height: 32px;
      color:$font-color;
      font-weight: 300;

      @include tablet {
        font-size: 24px;
      }

      @include mobile {
        font-size: 16px;
      }

      & a {
        color: $link-color;
        text-decoration: none;
        &:hover {
          text-decoration: underline;
        }
      }
    }

  }

}