@import "src/variables";

.Block {
  background-color: white;
  border-radius: 12px;
  padding: 24px;
  display: flex;
  gap: 24px;

  &Body {

    &Title {
      margin: 0;
      font-size: 40px;
      color: $font-color;
      font-weight: 400;
    }

    &Content {
      font-size: 28px;
      line-height: 32px;
      color:$font-color;
      font-weight: 300;

      @include tablet {
        font-size: 24px;
      }

      @include mobile {
        font-size: 16px;
      }

      & a {
        color: $link-color;
        text-decoration: none;
        &:hover {
          text-decoration: underline;
        }
      }
    }

    &Image {
      @include tablet {
        display: none;
      }
      @include mobile {
        display: none;
      }
    }

  }
}