@import "src/variables";

.Carousel {

  &Content {
    display: flex;
    flex-direction: column;
    gap: 24px;

    &Inactive {
      @include web {
        display: none;
      }
    }
  }

  &Switch {
    display: none;
    @include web {
      display: flex;
    }
    align-items: center;
    justify-content: center;
    gap: 8px;

    &Page {
      margin-top: 24px;
      border-radius: 8px;
      width: 32px;
      height: 10px;
      cursor: pointer;
      background-color: rgba($font-color, 0.4);

      &:hover {
        background-color: rgba($font-color, 0.7);
      }

      &Active {
        background-color: rgba($font-color, 0.7);
      }
    }
  }

}
